import * as Yup from 'yup';

export const validationAuthorizationScheme = Yup.object().shape({
  login: Yup.string()
    .required('Field is required')
    .matches(
      /^[^\s@]+@[^\s@]+\.[^\s@]+$|^\+?[0-9]+$/,
      'Enter a valid LogIn (email or valid phone number)'
    ),
  password: Yup.string()
    .required('Field is required')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
});
