import * as Yup from 'yup';

export const validRegistrationPhoneNumberScheme = Yup.object().shape({
  primaryPhoneNumber: Yup.string()
    .required('Field is required')
    .matches(/^\+380[0-9]{9}$/, 'Invalid phone number format'),
  password: Yup.string()
    .required('Field is required')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
});
