import * as Yup from 'yup';

export const validationProductSchema = Yup.object().shape({
  videoUrl: Yup.string()
    // .required("  ' ")
    .matches(
      /^(https?:\/\/)?(www\.)?(youtube\.com\/watch\?v=|youtu\.be\/)([a-zA-Z0-9_-]{11})$/,
      '    YouTube '
    ),
  name: Yup.string()
    // .required("   ' ")
    .min(3, '     3 ')
    .max(50, '     50 '),
  description: Yup.string()
    .min(3, '     3 ')
    .max(300, '      300 '),
  brend: Yup.string()
    // .required("   ' ")
    .min(3, '     3 ')
    .max(50, '     50 '),
});
